import PropTypes from 'prop-types'
import styles from './styles.module.scss'
import {
  FormikTextField,
  FormikSelect,
  FormikCheckbox,
  FormikAutocomplete
} from 'components/FormikField'
import { questionTypes } from '../../../../utils'
import { questionPriceHint, questionTypesHint, realPriceHint, bagcatRealPriceTypeHint } from './hints'
import WithHint from './WithHint'
import Typography from '@mui/material/Typography'

FormFields.propTypes = {
  formik: PropTypes.object,
  submitting: PropTypes.bool
}
export default function FormFields({ formik, submitting }) {
  const fieldProps = {
    formik, disabled: submitting
  }

  const numberFieldProps = {
    ...fieldProps,
    type: 'number',
    InputProps: { inputProps: { min: 0, max: 4294967295 } }
  }

  return (
    <>
      <Typography variant='h6'>  </Typography>
      <WithHint hint={questionPriceHint}>
        <FormikTextField name='price' label='' {...numberFieldProps} />
      </WithHint>
      <WithHint hint={questionTypesHint}>
        <FormikSelect name='type' label=' ' options={questionTypes} {...fieldProps} />
      </WithHint>
      {formik.values.type === 'cat' && <WithHint hint={realPriceHint}>
        <FormikTextField name='realprice' label='  ' {...numberFieldProps} />
      </WithHint>}
      {formik.values.type === 'bagcat' && <>
        <WithHint hint={bagcatRealPriceTypeHint}>
          <FormikSelect
            name='questionPriceType'
            label='   '
            options={{
              'fixed': ' ',
              'minMax': '         ',
              'byPlayer': '      '
            }}
            {...fieldProps}
          />
        </WithHint>
        {formik.values.questionPriceType === 'fixed' && <WithHint hint={realPriceHint}>
          <FormikTextField name='realprice' label='  ' {...numberFieldProps} />
        </WithHint>}
        {formik.values.questionPriceType === 'byPlayer' && <WithHint hint={realPriceHint}>
          <div className={styles.grouped}>
            <FormikTextField name='realpriceFrom' label='' {...numberFieldProps} />
            <FormikTextField name='realpriceTo' label='' {...numberFieldProps} />
            <FormikTextField name='realpriceStep' label='' {...numberFieldProps} />
          </div>
        </WithHint>}
      </>}
      {formik.values.type === 'bagcat' && <>
        <FormikCheckbox name='transferToSelf' label='   ' {...fieldProps} />
        <WithHint hint={<p>   <b></b>,    ,     </p>}>
          <FormikSelect
            name='detailsDisclosure'
            label='   '
            options={{
              'before': '  ',
              'after': '  ',
              'never': ''
            }}
            {...fieldProps}
          />
        </WithHint>
      </>}
      <Typography variant='h6'></Typography>
      <FormikAutocomplete
        name='correctAnswers'
        formik={formik}
        label=' '
        disabled={submitting}
      />
      <FormikAutocomplete
        name='incorrectAnswers'
        formik={formik}
        label='  ()'
        disabled={submitting}
      />
    </>
  )
}
