import React from 'react'
import PropTypes from 'prop-types'
import styles from './styles.module.scss'
import { useFormik } from 'formik'
import * as yup from 'yup'
import { componentsPropTypes, format } from '../../../consts'
import {
  FormikTextField,
  FormikAutocomplete,
  FormikSelect,
  FormikCheckbox,
  FormikSlider,
  FormikImageField
} from 'components/FormikField'
import { connect } from 'react-redux'
import Button from '@mui/material/Button'
import { useBeforeunload } from 'react-beforeunload'
import { useHistory, Prompt } from 'react-router-dom'
import { saveLocalPack } from 'localStorage/localPacks'
import { initValues, mapPackState } from '../../../utils'

const validationSchema = yup.object({
  logo: yup
    .string('  '),
  name: yup
    .string('  ')
    .required('  '),
  authors: yup
    .array()
    .min(0, '  '),
  publisher: yup
    .string(' '),
  difficulty: yup
    .number('  ')
    .positive()
    .min(1)
    .max(10)
    .integer('   ')
    .required('  '),
  comment: yup
    .string(' '),
  tags: yup
    .array(' '),
  language: yup
    .string('  ')
    .required('  '),
  over18: yup
    .bool('  ')
})

Settings.propTypes = {
  pack: PropTypes.shape(componentsPropTypes),
  dispatch: PropTypes.func
}

function Settings(props) {
  const [submitting, setSubmitting] = React.useState(false)
  const history = useHistory()
  const initialValues = initValues(validationSchema, props.pack)

  const formik = useFormik({
    initialValues,
    validationSchema,
    validateOnChange: false,
    validateOnBlur: false,
    onSubmit: async (values) => {
      setSubmitting(true)
      let pack = { ...props.pack, ...values }
      await saveLocalPack(pack)
      props.dispatch({ type: 'pack/load', pack })
      history.push(`/pack/${pack.uuid}`)
    },
  })

  useBeforeunload((event) => {
    if(Object.keys(formik.touched).length) event.preventDefault()
  })

  return (
    <div className={styles.container}>
      <form onSubmit={formik.handleSubmit} className={styles.form}>
        <FormikTextField
          name='name'
          formik={formik}
          label=''
          disabled={submitting}
        />
        <FormikAutocomplete
          name='authors'
          formik={formik}
          label=''
          disabled={submitting}
        />
        <FormikImageField
          name='logo'
          formik={formik}
          label=''
        />
        <FormikTextField
          name='publisher'
          formik={formik}
          label='  ()'
          disabled={submitting}
        />
        <FormikSlider
          name='difficulty'
          label=''
          formik={formik}
          disabled={submitting}
          step={1} min={1} max={10}
        />
        <FormikSelect
          name='language'
          formik={formik}
          label=' '
          options={{
            '': ' ',
            'ru-RU': ' (ru-RU)',
            'en-US': ' (en-US)'
          }}
          disabled={submitting}
        />
        <FormikTextField
          name='comment'
          formik={formik}
          label=' ()'
          disabled={submitting}
        />
        <FormikAutocomplete
          name='tags'
          formik={formik}
          label=' ()'
          disabled={submitting}
        />
        <FormikCheckbox
          name='over18'
          formik={formik}
          label='  18+'
          disabled={submitting}
        />
        <Button
          color='primary'
          variant='contained'
          type='submit'
          disabled={!Object.keys(formik.touched).length || submitting}
        >
          
        </Button>
      </form>
      <Prompt
        when={Object.keys(formik.touched).length && !submitting}
        message='   ,   ?'
      />
      <div className={styles.info}>
        <ul>
          <li> XML   : <span>{format.xmlVersion}</span></li>
          <li>   : <span>{format.encoding}</span></li>
          <li> : <span>{props.pack.version}</span></li>
          <li>UUID: <span>{props.pack.uuid}</span></li>
          <li> : <span>{props.pack.date}</span></li>
        </ul>
      </div>
    </div>
  )
}

export default connect(mapPackState)(Settings)
